

 .spin-page-container {
   position: relative;
   min-height: 100vh;
   padding-bottom: 60px;
   background-color: var(--bg-color, #f8f9fa);
   color: var(--text-color, #000);
 }

 .wheel-container {
   position: relative;
   width: 300px;
   height: 300px;
   margin: 0 auto;
   border-radius: 50%;
   border: 8px solid #444;
   overflow: hidden;
 }

 .wheel {
   width: 100%;
   height: 100%;
   border-radius: 50%;
   position: absolute;
   top: 0;
   left: 0;
   transition: transform 5s cubic-bezier(0.33, 1, 0.68, 1);
 }

 .segment {
   position: absolute;
   width: 50%;
   height: 50%;
   top: 50%;
   left: 50%;
   transform-origin: 0% 0%;
   display: flex;
   justify-content: flex-end;
   align-items: center;
   padding-right: 10px;
   color: white;
   font-weight: bold;
   font-size: 14px;
 }

 .segment-label {
   transform: rotate(-90deg);
 }

 .pointer {
   position: absolute;
   top: -20px;
   left: 50%;
   transform: translateX(-50%);
   font-size: 24px;
   color: red;
 }

 .karol-float {
   position: absolute;
   width: 50px;
   height: auto;
   animation: float 3s ease-in-out forwards;
   pointer-events: none;
   z-index: 10;
 }

 @keyframes float {
   0% {
     opacity: 1;
     transform: translateY(0);
   }
   100% {
     opacity: 0;
     transform: translateY(-200px);
   }
 }